import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n);

const DEFAULT_LANG = 'zh-TW-ct';
export const AU_LOCALE_KEY = 'AU_LANG';

const locales = {
    'en-US': require('./locales/en.json'),
    'zh-CN': require('./locales/zh-Hans.json'),
    'zh-TW': require('./locales/zh-Hant.json'),
    'en-US-ct': require('./locales/en-ct.json'),
    'zh-CN-ct': require('./locales/zh-Hans-ct.json'),
    'zh-TW-ct': require('./locales/zh-Hant-ct.json'),
};

const i18n = new VueI18n({
    locale: localStorage.getItem(AU_LOCALE_KEY) ? localStorage.getItem(AU_LOCALE_KEY) : DEFAULT_LANG,
    messages: locales,
});

Vue.prototype.$setupLang = function setupLang(lang) {
    if (window.zE) {
        window.zE(function () {
            if (lang === 'zh-TW' || lang === 'zh-TW-ct') {
                window.zE.setLocale('zh-tw');
            } else if (lang === 'zh-CN' || lang === 'zh-CN-ct') {
                window.zE.setLocale('zh-cn');
            } else {
                window.zE.setLocale(lang);
            }
        });
    }

    if (lang === undefined) {
        lang = window.localStorage.getItem(AU_LOCALE_KEY);
        if (locales[lang] === undefined) {
            lang = DEFAULT_LANG
        }
    }

    window.localStorage.setItem(AU_LOCALE_KEY, lang.replace('-ct', ''));

    Object.keys(locales).forEach(lang => {
        document.body.classList.remove(`lang-${lang}`)
    });
    document.body.classList.add(`lang-${lang}`)
    document.body.setAttribute('lang', lang);

    Vue.config.lang = lang;
    i18n.locale = lang;
    // update the lang to router
    if (this.$route.params.lang !== lang.replace('-ct', '')) {
        this.$router.push({name: this.$route.name, params: {lang: lang.replace('-ct', '')}, query: this.$route.query})
    }
};
export default i18n
