import Vue from 'vue'
import App from './App.vue'
import Antd from 'ant-design-vue'
import 'ant-design-vue/dist/antd.css'
import request, {AU_UUID, AU_HMAC, AU_IN_APP, AU_IN_APP_TRIAL} from "./requestServer";
import evnet from "./eventFire";
import clipboard from 'v-clipboard';
import i18n from './i18n';
import router from './router';
import { DatePicker } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import lottie from 'vue-lottie';
import VueAwesomeSwiper from "vue-awesome-swiper";
import { Swiper as SwiperClass, Pagination, Mousewheel, Autoplay } from 'swiper/swiper.esm';
import getAwesomeSwiper from 'vue-awesome-swiper/dist/exporter';
SwiperClass.use([Pagination, Mousewheel, Autoplay]);
Vue.use(getAwesomeSwiper(SwiperClass));
const { Swiper, SwiperSlide } = getAwesomeSwiper(SwiperClass);
import 'swiper/swiper-bundle.css';
import '@/assets/css/animate.min.css';/**/

import printJS from '@/plugs/print';
import Print from 'vue-print-nb';


import eventBus from "./eventBus";

Vue.component('lottie', lottie);
Vue.config.productionTip = false;
Vue.prototype.$request = request;
Vue.prototype.$eventFire = evnet;
Vue.prototype.$eventBus = eventBus
Vue.use(Antd);
Vue.use(clipboard);
Vue.use(DatePicker);
Vue.use(VueAwesomeSwiper);

Vue.use(printJS);
Vue.use(Print);

Vue.component('Swipter', Swiper);
Vue.component('SwiperSlide', SwiperSlide);

Vue.prototype.$message.config({
  top: `100px`,
  left: `500px`,
  maxCount: 1,
});
Vue.prototype.$isPc = function() {
  let width = document.documentElement.clientWidth || document.body.clientWidth;
  return width > 500;
  //return !navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
};
Vue.prototype.$height = function() {
  return document.documentElement.clientHeight || document.body.clientHeight;
};
Vue.prototype.$isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
Vue.prototype.$zUrl = process.env.NODE_ENV === 'production' || process.env.NODE_ENV === 'development'? window.location.origin : `https://au.willet.cool`;

new Vue({
  i18n,
  router,
  render: h => h(App),
}).$mount('#app')
