import Vue from 'vue'
import VueRouter from 'vue-router'
import i18n, {AU_LOCALE_KEY} from './i18n';
import {
    AU_SELECTED_SERVICE,
    AU_UUID,
    AU_HMAC,
    AD_PLATFORM,
    AD_CLICKID,
    ADS_PLATFORM,
    AU_AFF,
    AU_IN_APP_TRIAL,
    AU_IN_APP,
    AU_AUAD_ID, ADS_CLICK_KEY, AU_PLATFORM,
} from "./requestServer";
import * as _ from 'lodash';

Vue.use(VueRouter)

/**/
const routes = [

    {
        path: '/:lang',
        name: 'root',
        component: () => import('./App'),
        children: [
            {
                path: 'landing-page',
                name: 'landing-page',
                redirect: to => {
                    const u = navigator.userAgent;
                    if (
                        (to.query && (to.query.wbraid || to.query.gbraid || to.query.gclid || to.query.fbclid || to.query.clickid))
                        && (u.indexOf('Android') > -1 || u.indexOf('Adr') > -1)
                    ) {
                        return {name: 'android-app'}
                    } else {
                        return {name: 'home'}
                    }

                }
            },
            {
                path: 'landing-page/android-app',
                name: 'android-app',
                component: () => import("./views/AndroidAds")
            },
            {
                path: '',
                name: 'service-panel',
                component: () => import('./views/AUPanelHome'),
                children: [

                    {
                        path: '/',
                        name: 'home',
                        component: () => import('./views/Home')
                    },
                    {
                        path: 'user/login',
                        name: 'login',
                        component: () => import('./components/Login'),
                    },
                    {
                        path: 'user/service/:serviceId',
                        name: 'service',
                        component: () => import('./views/MyService'),
                    },
                    {
                        path: 'user/service/:serviceId/invoice',
                        name: 'manageBill',
                        component: () => import('./views/ManageBill')
                    },
                    {
                        path: 'user/account',
                        name: 'manageAccount',
                        component: () => import('./views/ManageAccount')
                    },
                    {
                        path: 'user/invoice/:invoiceId',
                        name: 'invoiceDetail',
                        component: () => import('./views/InvoiceDetail')
                    },
                    {
                        path: 'order/place',
                        name: 'product',
                        component: () => import('./views/Product')
                    },
                    {
                        path: 'order/pay',
                        name: 'payment',
                        component: () => import('./views/PaymentPage')
                    },
                    {
                        path: 'help',
                        name: 'support',
                        component: () => import('./views/Support'),
                        /*beforeEnter: (to, from, next) => {
                            document.title = i18n.t('help:seo.TitleTag');
                            document.querySelector('meta[name="keywords"]').setAttribute('content', i18n.t('help:seo.keywords'));
                            document.querySelector('meta[name="description"]').setAttribute('content', i18n.t('help:seo.MetaDescription'));
                            next();
                        },*/
                        children: [
                            {
                                path: 'article/:articleId',
                                name: 'article',
                                component: () => import('./components/SupportArticle.vue')
                            },
                            {
                                path: 'contact-us',
                                name: 'contact-us',
                                component: () => import('./components/SupportContactUsView.vue')
                            }
                        ]
                    },
                ]
            },
        ]
    }
];

/**/
const router = new VueRouter({
    mode: "history",   //
    routes //  routes: routes
});
router.beforeEach((to, from, next) => {
    let uuid = '';
    let hmac = '';

    if (to.query) {
        if (to.query.uuid) {
            uuid = to.query.uuid;
            localStorage.setItem(AU_UUID, uuid);
        }
        if ( to.query.hmac) {
        hmac = to.query.hmac;
        localStorage.setItem(AU_HMAC, hmac);}

        if (to.query['utm_source']) {
            let platform = to.query['utm_source']
            if (ADS_PLATFORM.indexOf(platform) !== -1) { // save only we use
                localStorage.setItem(AD_PLATFORM, platform)
                // try to find the specialPlatform like googleads/facebookads

                let specialPlatform = ADS_CLICK_KEY.find(function (item) {
                    return item.platform === platform
                })
                console.log(specialPlatform)
                if (specialPlatform !== undefined) {
                    for (let key of specialPlatform.keys) {
                        if (to.query[key]) {
                            localStorage.setItem(AD_CLICKID, to.query[key])
                            break;
                        }
                    }
                } else if (to.query['clickid']){
                    localStorage.setItem(AD_CLICKID, to.query['clickid'])
                }
            }
        }


        if (to.query.aff) {
            localStorage.setItem(AU_AFF, to.query.aff);
        }
        if (to.query.auadid) {
            localStorage.setItem(AU_AUAD_ID, to.query.auadid);
        }
        if (to.query.inAppTrial) {
            localStorage.setItem(AU_IN_APP_TRIAL, to.query.inAppTrial);
        }
        if (to.query.inApp) {
            localStorage.setItem(AU_IN_APP, to.query.inApp);
        }
        if (to.query.platform) {
            localStorage.setItem(AU_PLATFORM, to.query.platform)
        }
    }
    if (to.params.lang !== undefined) {
        switch (to.params.lang) {
            case 'en-US'://set lang to local
                window.localStorage.setItem(AU_LOCALE_KEY, 'en-US');
                break;
            case 'zh-CN':
                window.localStorage.setItem(AU_LOCALE_KEY, 'zh-CN');
                break;
            case 'zh-TW':
                window.localStorage.setItem(AU_LOCALE_KEY, 'zh-TW');
                break;
            default : {
                if (window.localStorage.getItem(AU_LOCALE_KEY) !== null) {
                    next({
                        path: `${window.localStorage.getItem(AU_LOCALE_KEY)}${to.path}`,
                        params: {lang: window.localStorage.getItem(AU_LOCALE_KEY)},
                        query: to.query
                    });
                } else {
                    window.localStorage.setItem(AU_LOCALE_KEY, 'zh-TW')
                    next({
                        path: `zh-TW${to.path}`,
                        params: {lang: 'zh-TW'},
                        query: to.query
                    });
                }
                return;
            }
        }
    } else {//
        if (window.localStorage.getItem(AU_LOCALE_KEY) === null) {
            window.localStorage.setItem(AU_LOCALE_KEY, 'zh-TW');
        }
        // Case: go to user panel when bring the info and the to.path is main page
        if (uuid && hmac && to.path === '/') {
            next({
                path: `${window.localStorage.getItem(AU_LOCALE_KEY)}/user/service`,
                name: 'service',
                params: {
                    lang: window.localStorage.getItem(AU_LOCALE_KEY),
                    serviceId: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0
                },
                query: to.query
            });
        } else {
            next({
                path: `${window.localStorage.getItem(AU_LOCALE_KEY)}${to.path}`,
                params: {lang: window.localStorage.getItem(AU_LOCALE_KEY), ...to.params},
                query: to.query
            });
        }

        // if (invoiceId && uuid && hmac) {
        //     next({ path: `${window.localStorage.getItem(AU_LOCALE_KEY)}${to.path}`,
        //         params: {lang: window.localStorage.getItem(AU_LOCALE_KEY)},
        //         query: {'invoiceId': invoiceId}});
        // } else if (uuid && hmac) {
        //     next({
        //         path: 'user/service',
        //         name: 'service',
        //         params: {
        //             lang: window.localStorage.getItem(AU_LOCALE_KEY),
        //             serviceId: localStorage.getItem(AU_SELECTED_SERVICE) ? JSON.parse(localStorage.getItem(AU_SELECTED_SERVICE)).id : 0
        //         },
        //         query: to.query
        //     });
        // } else if (exotracker || gclId || fbclid || aff) {
        //     next({ path: `${window.localStorage.getItem(AU_LOCALE_KEY)}${to.path}`,
        //         params: {lang: window.localStorage.getItem(AU_LOCALE_KEY)},query: to.query});
        // } else {
        //     next({ path: `${window.localStorage.getItem(AU_LOCALE_KEY)}${to.path}`,
        //         params: {lang: window.localStorage.getItem(AU_LOCALE_KEY)},
        //         query: to.query});
        // }
        return;
    }
    if (to.name === 'home') {
        document.title = i18n.t('home:seo.TitleTag');
        document.querySelector('meta[name="keywords"]').setAttribute('content', i18n.t('home:seo.keywords'));
        document.querySelector('meta[name="description"]').setAttribute('content', i18n.t('home:seo.MetaDescription'));
    } else if (to.name === 'product') {
        document.title = i18n.t('order/place:seo.TitleTag');
        document.querySelector('meta[name="keywords"]').setAttribute('content', i18n.t('order/place:seo.keywords'));
        document.querySelector('meta[name="description"]').setAttribute('content', i18n.t('order/place:seo.MetaDescription'));
    } else if (to.name === 'support') {
        document.title = i18n.t('help:seo.TitleTag');
        document.querySelector('meta[name="keywords"]').setAttribute('content', i18n.t('help:seo.keywords'));
        document.querySelector('meta[name="description"]').setAttribute('content', i18n.t('help:seo.MetaDescription'));
    } else if (to.name === 'payment') {
        document.title = 'auVPN';
    } else if (to.name === 'service') {
        document.title = 'auVPN';
    } else if (to.name === 'manageBill') {
        document.title = 'auVPN';
    } else if (to.name === 'manageAccount') {
        document.title = 'auVPN';
    } else if (to.name === 'invoiceDetail') {
        document.title = 'auVPN';
    } else if (to.name === 'login') {
        document.title = 'auVPN';
    } else if (to.name === 'android-app') {
        document.title = 'VPN  auVPN';
    }

    // Case: hide the uuid&hmac if the path not the user
    if (uuid && hmac && to.path.indexOf('user') === -1) {
        next({
            path: to.path,
            // name: to.name,
            query: _.omit(to.query, ['uuid', 'hmac'])
        })
    } else {
        next({query: to.query});
    }
});
/**/
router.afterEach((to, from, next) => {
    window.scrollTo(0, 0)
});
/**/
export default router
